.main-nav{
    padding: 25px 30px;
    transition: .3s ease .02s;
    .navbar-nav{
        .nav-item{
            .nav-link{
                color: $white;
                padding-left: 20px;
                padding-right: 20px;
                font-size: 15px;
                text-transform: capitalize;
                letter-spacing: 1px;
                position: relative;
                z-index: 1;
                font-family: $primary-font;
                font-weight: 500;
            }
        }
    }
    .navbar-brand{
        margin-right: 0;
        text-align: center;
        img{
            max-width: 150px;
        }
    }
    .dropdown-menu {
        padding: 10px;
        border: none;
        border-radius: 4px;
        max-width: 350px;
        box-shadow: 4px 13px 25px 1px rgba(0,0,0,.1);
        .dropdown-item{
            font-family: $primary-font;
            color: $text-color;
            padding: 10px 20px;
            font-size: 15px;
            font-weight: 500;
        }
    }
    .navbar-toggler{
        outline: 0;
    }
    &.nav-bg{
        background: $primary-color;
        padding: 20px;
        border: none;
        box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.09);
    }

}

// Headroomjs Styles
.headroom {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: all .5s ease-in-out;
}
.headroom--unpinned {
    top: -150px;
    @include desktop{
        top: 0;
    }
}
.headroom--pinned {
    top: 0;
}