.hero__area{
    padding: 200px 0 150px;
    background-image: $primary-gradient;
    position: relative;
    &-content{
        h2, p{
            color: $white;
        }
        h2{
            margin-bottom: 20px;
        }
        p{
            width: 70%;
            margin-bottom: 30px;
        }
    }
    &-images{
        position: relative;
        height: 630px;
        .floating-illu{
            position: absolute;
            box-shadow: 4px 13px 25px 1px rgba(0,0,0,.1);
        }
        .large{
            top: 0; right: 0;
            img{
                width: 280px;
            }
        }
        .medium{
            left: 0; bottom: 40px;
            img{
                width: 270px;
            }
        }
        .small{
            right: 20px; bottom: 0;
            img{
                width: 300px;
            }
        }
    }
    .scroll__down{
        position: absolute;
        left: 50%; transform: translateX(-50%);
        bottom: 40px;
        i{
            font-size: 30px;
            color: $white;
        }
    }
    .bottom-curve{
        position: absolute;
        width: 100%;
        bottom: 0; left: 0;
        img{
            width: 100%;
        }
    }
}