body{
    background: $body-color;
}

.section{
    padding: 100px 0;
    &__title-center{
        margin-bottom: 80px;
        position: relative;
        &::before{
            position: absolute;
            content: '';
            width: 100px; height: 10px;
            bottom: -10px; left: 50%; transform: translateX(-50%);
            background-color: $secondary-color;
            border-radius: 10px;
        }
    }
}

a{
    color: $primary-color;
}