// Color Variables
$body-color: #fafafa;
$white: #fff;
$primary-color: #22d5da;
$secondary-color: #c7a7f7;
$black: #000;
$text-color: #777777;
$text-color-dark: #182345;
$text-color-light: #acacac;
$border-color: #dedede;
$footer-background-color: #272E34;

// Gradients
$primary-gradient: linear-gradient(120deg,#31b6ff,#06e9c1);
$secondary-gradient: linear-gradient( 90deg, rgb(109,167,251) 0%, rgb(63,131,243) 100%);
$violate-gradient: linear-gradient( 67deg, rgb(148,62,199) 0%, rgb(60,55,191) 100%);

// Font Variables
$primary-font: 'Ubuntu', sans-serif;
$secondary-font: 'Roboto', sans-serif;
$icon-font: 'themify';

// Shadows
$main-shadow : 4px 13px 25px 1px rgba(0,0,0,.1);