.blog{
    background-image: $primary-gradient;
    .section__title{
        margin-bottom: 80px;
    }
    &__post{
        border-radius: 6px;
        overflow: hidden;
        background: $white;
        margin-bottom: 50px;
        .post-content{
            padding: 30px;
            .post-title{
                margin-bottom: 20px;
            }
            .post-excerpt{
                margin-bottom: 20px;
            }
            a{
                font-family: $primary-font;
                color: $text-color-dark;
                font-size: 18px;
                font-weight: 500;
            }
        }
    }
}