.step{
    background-image: $primary-gradient;
    .section__title{
        margin-bottom: 100px;
    }
    &__item{
        background: $white;
        padding: 50px 40px;
        border-radius: 6px;
        box-shadow: $main-shadow;
        position: relative;
        margin-bottom: 50px;
        .number{
            position: absolute;
            font-size: 40px;
            width: 80px; height: 80px;
            display: block;
            background: $white;
            text-align: center;
            line-height: 80px;
            box-shadow: $main-shadow;
            border-radius: 100%;
            top: -30px; left: -30px;
            font-family: $primary-font;
            font-weight: 700;
        }
        h3{
            margin-bottom: 30px;
        }
    }
    .trialbox{
        padding: 50px 20px;
        background: $white;
        border-radius: 6px;
        box-shadow: $main-shadow;
    }
}