.service{
    padding: 100px 0;
    &__item{
        padding: 100px 0;
    }
    &__content{
        h3{
            margin-bottom: 30px;
        }
        p{
            margin-bottom: 30px;
        }
    }
    &__image{
        position: relative;
        height: 450px;
        .image{
            position: absolute;
            img{
                box-shadow: $main-shadow;
            }
        }
        &.followers{
            .right{
                right: 0;
                top: 50%; transform: translateY(-50%);
            }
            .left-top{
                bottom: 250px;
                left: 0;
                img{
                    border-radius: 100px;
                    max-width: 175px;
                }
            }
            .left-middle{
                bottom: 185px; left: 185px;
                img{
                    border-radius: 100px;
                    max-width: 175px;
                }
            }
            .left-bottom{
                bottom: 100px; left: 20px;
                img{
                    border-radius: 100px;
                    max-width: 175px;
                }
            }
        }
        &.likes{
            .left{
                left: 0; top: 50%; transform: translateY(-50%);
            }
            .right{
                right: 0;
                bottom: 0;
                img{
                    max-width: 350px;
                }
            }
        }
    }
    &__title-secondary{
        position: relative;
        &::before{
            content: '';
            position: absolute;
            width: 80px; height: 10px;
            background-color: $secondary-color;
            z-index: -1;
            border-radius: 10px;
            bottom: -10px; left: 0;
        }
    }
    &__title-primary{
        position: relative;
        &::before{
            content: '';
            position: absolute;
            width: 80px; height: 10px;
            background-color: $primary-color;
            z-index: -1;
            border-radius: 10px;
            bottom: -10px; right: 0;
        }
    }
}